import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import LazyLoad from 'react-lazy-load';
// Import Style
import { BtnViewAll } from './styled';

const Button = ( props ) => (
	<LazyLoad>
	  	<BtnViewAll>
	 		<span>View All</span>
			<FontAwesomeIcon icon="arrow-right" />
	  	</BtnViewAll>
	</LazyLoad>
);

export default(Button);
